<template>
  <div id="legaltrade-box" :class="skins=='days'?'whites':'whites black'">
    <div class="content">
      <h3>步骤一：提交申请</h3>
      <p>买卖商户必须为平台认证商户，并在本页面点击“确认申请”按钮，提交申请，并同意冻结
        <span class="lock">{{tobeSellerLockusdt}}USDT</span>作为商家保证金。
      </p>
      <h3>步骤二：资料审核</h3>
      <p>我们系统自动审核商家申请资料及匹配以往交易，确认符合商家申请条件，您可以在法币交易区发布广告。如申请条件未符合，我们将12小时内联系您，请保持通讯畅通。</p>
      <div class="remember-password mt40">
        <input v-model="checkboxValue" type="checkbox" name id>
        <span class="white">同意冻结
          <b class="lock">{{tobeSellerLockusdt}}USDT</b>作为商家保证金
        </span>
      </div>
			<div class="application-btn" @click="application">申请成为商家</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
			checkboxValue:false,
			token:'',
			tobeSellerLockusdt:'',
			skins: localStorage.getItem("skin") || "days"
		};
  },
  created() {
		let that = this;
    let token = window.localStorage.getItem("token") || "";
    if (token) {
      that.token = token;
		}
		this.$http({
					url: "/api/user/info",
					headers: {
						Authorization: that.token
					}
				}).then(res => {
					if (res.data.type == "ok") {
						that.tobeSellerLockusdt = res.data.message.tobe_seller_lockusdt
						
					}
				});
	},
	beforeCreate () {
			if (
    localStorage.getItem("skin") &&
    localStorage.getItem("skin") == "nights"
    ) {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#000;");
    } else {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#f8f6f6;");
    }
		},
		beforeDestroy () {
			document.querySelector('html').removeAttribute('style')
		},
  methods: {
		application(){
			let that = this;
			if(that.checkboxValue){
				that.$router.push({path:'/LegalApplicationData'});
			}else{
				layer.msg('请勾选同意冻结');
			}
		}
	}
};
</script>

<style lang='scss' scoped>
$purple: #f0b90b;
.whites{
	width: 1200px;
	margin: 66px auto 80px;
	background-color: #fff;
	min-height: 680px;
	padding-bottom: 30px;
	position: relative;
	top: 40px;
}
.content{
	width: 900px;
	margin:0 auto 60px;
	padding-top: 20px;

	h3{
		font-size: 20px;
		line-height: 60px;
	}
	p{
		line-height: 1.8
	}
	input{
		position: relative;
		top: 2px;
		margin-right: 10px;
	}
	.application-btn{
		background: #f0b90b;
    line-height: 40px;
    width: 150px;
    color: #ffffff;
		cursor: pointer;
		text-align: center;
		border-radius: 6px;
		margin-top: 50px;
	}
}
.black{
	background-color: #1e2b34;
	color: #fff;
}
.black .white{
	color: #fff;
}
</style>



